.city {
  filter: brightness(0.6);
  background: url("../Assests/skyline.png") center bottom no-repeat;
  background-size: cover;
  width: 100%;
  height: 70%;
  position: absolute;
  bottom: 0;
  z-index: 3;
}

@media screen and (max-width: 830px) {
.city{
  width: 100%;
  height:50%;
}

}